.wrapper {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
  max-width: 1300px;
  overflow-x: hidden;

  &-block {
    padding: 20px;
  }
}

.content-block__wrapper {
  padding: 20px;
}

main.main {
  @media (max-width: 768px) {
    width: 100vw;
    overflow: hidden;
  }
}
.page-template-free-spins-page .main {
  @media (max-width: 768px) {
    margin-top: 61px;
  }
}
body {
  overflow-x: hidden;
  background: #f3f3f3;
}

button {
  background: none;
  outline: none;
  border: none;
}

input {
  background: none;
  outline: none;
  border: none;
}

textarea {
  background: none;
  outline: none;
  border: none;
}

.main {
  margin-top: 50px;
  section {
    margin-top: 50px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}
