.btn {
  display: inline-block;
  border-radius: 6px;
  padding: 9px 14px;
  background-color: $yellow;
  overflow: hidden;
  text-align: center;
  position: relative;
  transform: translateY(0);
  transition: .3s transform;
  font-size: 14px;
  line-height: 20px;
  span:not(.spinner-wrapper) {
    color: $text-title;
    font-family: $font-extrabold;
    font-size: 14px;
    line-height: 20px;
    position: relative;
    z-index: 2;
  }
  &:after {
    content: '';
    display: block;
    width: 150%;
    height: 100%;
    position: absolute;
    top: 0;
    right: -155%;
    z-index: 1;
    background-color: #EFD34D;
    transition: .3s;
    transform: skewX(10deg);
  }
  &:hover {
    transform: translateY(2px);
    &:after {
      right: -10%;
    }
  }

  .spinner-wrapper {
    position: absolute;
    left: 32px;
  }
}
