body {
  color: $text-desc;
  font-family: $font-regular;
  font-size: 16px;
  line-height: 150%;
  p {
    color: $text-desc;
    font-family: $font-regular;
    font-size: 16px;
    line-height: 150%;
    margin-bottom: 12px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  li {
    color: $text-desc;
    font-family: $font-semibold;
    font-size: 16px;
    line-height: 150%;
  }
}

h1, h2, h3, h4, h5, h6, .h1, .h2, .h2, .h3, .h4, .h5, .h6, .h7 {
  color: $text-title;
  font-family: $font-extrabold;
  margin-bottom: 12px;
  line-height: normal;
  &:first-child {
    margin-top: 0;
  }
  &:last-child {
    margin-bottom: 0;
  }
}

h1, .h1 {
  font-size: 32px;
  line-height: 38px;
}

h2, .h2 {
  font-size: 28px;
  line-height: 32px;
}

h3, .h3 {
  font-size: 24px;
  line-height: 32px;
}

h4, .h4 {
  font-size: 20px;
  line-height: 30px;
}

h5, .h5 {
  font-size: 18px;
  line-height: 28px;
}

.block-heading a {
  font-family: $font-extrabold;
  color: $green;
  cursor: pointer;
}

p a, li a, ol a {
  font-family: $font-semibold;
  color: $green;
  cursor: pointer;
}

strong {
  font-family: $font-semibold;
}

ul:not(.plus-minus__list, .menu) {
  margin-bottom: 12px;
  list-style-type: disc;
  padding-left: 20px;
  &:last-child {
    margin-bottom: 0;
  }
  li:not(.plus-minus__elem, .menu-item) {
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}

ol:not(.numeric__list) {
  list-style: decimal;
  padding-left: 20px;
  margin-bottom: 12px;
  &:last-child {
    margin-bottom: 0;
  }
  li {
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}