.btn {
  display: inline-block;
  border-radius: 6px;
  padding: 9px 14px;
  background-color: #F9DD57;
  overflow: hidden;
  text-align: center;
  position: relative;
  transform: translateY(0);
  transition: 0.3s transform;
  font-size: 14px;
  line-height: 20px;
}
.btn span:not(.spinner-wrapper) {
  color: #1D232D;
  font-family: "Urbanist-ExtraBold";
  font-size: 14px;
  line-height: 20px;
  position: relative;
  z-index: 2;
}
.btn:after {
  content: "";
  display: block;
  width: 150%;
  height: 100%;
  position: absolute;
  top: 0;
  right: -155%;
  z-index: 1;
  background-color: #EFD34D;
  transition: 0.3s;
  transform: skewX(10deg);
}
.btn:hover {
  transform: translateY(2px);
}
.btn:hover:after {
  right: -10%;
}
.btn .spinner-wrapper {
  position: absolute;
  left: 32px;
}

