p a, .content-block li a, .content-block ol a, .block-heading a, .hover-link {
  transition: 0.3s;
  cursor: pointer;
  display: inline-block;
  position: relative;
}
p a:after, .content-block li a:after, .content-block ol a:after, .block-heading a:after, .hover-link:after {
  content: "";
  width: 100%;
  height: 1px;
  position: absolute;
  bottom: 4px;
  display: block;
  background: #3B7C0F;
  transform: translateY(3px) scale(0);
  transition: 0.3s;
}
p a:hover, .content-block li a:hover, .content-block ol a:hover, .block-heading a:hover, .hover-link:hover {
  color: #3B7C0F;
}
p a:hover:after, .content-block li a:hover:after, .content-block ol a:hover:after, .block-heading a:hover:after, .hover-link:hover:after {
  transform: translateY(3px) scale(1);
}
p a.white:after, .content-block li a.white:after, .content-block ol a.white:after, .block-heading a.white:after, .hover-link.white:after {
  background: #fff;
}
p a.white:hover, .content-block li a.white:hover, .content-block ol a.white:hover, .block-heading a.white:hover, .hover-link.white:hover {
  text-decoration: none;
  color: #fff;
}

.spinner-wrapper {
  display: flex;
  align-items: center;
}
.spinner-wrapper .spinner {
  width: 20px;
  height: 20px;
  display: none;
  border-radius: 50%;
  border-left: 0.2rem solid transparent;
  -moz-animation: rotating 0.8s linear infinite;
  -webkit-animation: rotating 0.8s linear infinite;
  animation: rotating 0.8s linear infinite;
}

@-webkit-keyframes rotating {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-moz-keyframes rotating {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes rotating {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.animation-sending .spinner {
  border-top: 0.2rem solid;
  border-right: 0.2rem solid;
  border-bottom: 0.2rem solid;
  display: inline-block;
  margin-right: 1.5rem;
}

